<template>
  <div class="controller">
    <div class="main">
      <div class="d-title">控制台</div>
      <div class="content display-flex">
        <div class="left">
          <ControllerRouter></ControllerRouter>
        </div>
        <div class="right">
          <router-view></router-view>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import ControllerRouter from '@/components/controller/ControllerRouter'
  export default {
    name: 'Controller',
    data() {
      return {

      }
    },
    components: {
      ControllerRouter
    }
  }
</script>

<style scoped>
  .content {
    justify-content: space-between;
    align-items: flex-start;
  }

  .left {
    width: 15%;
    background-color: #f9f9f9;
    box-sizing: border-box;
    padding: 20px;
  }

  .right {
    width: 83%;
    background-color: #f9f9f9;
    box-sizing: border-box;
    padding: 20px;
  }
</style>



